<template>
  <v-container
    fluid
    grid-list-md>
    <core-progress-modal :show="loading" />
    <v-layout
      v-if="!loading"
      row
      wrap>
      <v-flex md12>
        <v-form
          v-model="valid"
          novalidate
          enctype="multipart/form-data"
          @submit.prevent="validateBeforeSubmit">
          <v-card class="elevation-0">
            <v-card-title>
              <v-layout
                row
                wrap
              >
                <v-flex
                  lg10
                  md8
                  sm8
                >
                  <h3 class="headline mb-0">Recepcionar Lotes RPS</h3>
                </v-flex>
                <v-flex
                  v-if="mostrarBotoesDownload"
                  lg1
                  md2
                  sm2
                  class="layout justify-end mt-0"
                >
                  <v-btn
                    style="width: 50px; height: 50px;"
                    class="secondary"
                    title="Download XML"
                    @click="downloadXml()"
                  >
                    <v-layout
                      column>
                      <v-icon>mdi-cloud-download-outline</v-icon>
                      XML
                    </v-layout>
                  </v-btn>
                </v-flex>
                <v-flex
                  v-if="mostrarBotoesDownload"
                  lg1
                  md2
                  sm2
                  class="layout justify-end mt-0"
                >
                  <v-btn
                    style="width: 50px; height: 50px;"
                    class="primary"
                    title="Download Nota Fiscal"
                  >
                    <v-layout
                      column>
                      <v-icon>mdi-cloud-download-outline</v-icon>
                      <a
                        :href="form.linkNota"
                        class="white--text"
                        target="_blank"
                      >
                        NOTA
                      </a>
                    </v-layout>
                  </v-btn>
                </v-flex>
                <v-flex
                  v-if="disabled"
                  md12
                  class="layout align-center"
                >
                  <core-status-rps
                    :status="form.situacaoStr"
                    :mensagem="form.correcaoErro"
                  />
                  <span
                    v-if="form.correcaoErro"
                    class="caption">{{ form.correcaoErro }}</span>
                </v-flex>
              </v-layout>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md12>
                  <core-filtro-rede-empresa-loja
                    v-model="form"
                    :loja-selecionada="onChangeLoja"
                    :disabled="disabled"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model.trim="form.numeroRps"
                    :disabled="disabled"
                    label="Número RPS"
                    type="tel"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-validate="'required'"
                    v-model="form.dataEmissao"
                    :disabled="disabled"
                    :error-messages="errors.collect('Data Emissão')"
                    label="Data Emissão"
                    data-vv-name="Data Emissão"
                    type="date"
                    required
                  />
                </v-flex>
              </v-layout>
              <v-card class="elevation-0">
                <v-card-title>
                  <h3 class="headline mb-0">Serviço do RPS</h3>
                </v-card-title>
                <v-card-text>
                  <v-layout
                    row
                    wrap>
                    <v-flex md6>
                      <v-autocomplete
                        v-model="form.naturezaOperacao"
                        :disabled="disabled"
                        :items="listNatureza"
                        label="Natureza da Operação"
                        item-value="id"
                        item-text="descricao"
                      />
                    </v-flex>
                    <v-flex md3>
                      <v-text-field
                        v-formata-moeda="form.aliquota"
                        v-money="money"
                        v-validate="'required'"
                        v-model.lazy="form.aliquota"
                        :disabled="disabled"
                        :error-messages="errors.collect('Alíquota')"
                        data-vv-name="Alíquota"
                        type="tel"
                        prefix="%"
                        maxlength="5"
                        reverse
                        label="Alíquota"
                      />
                    </v-flex>
                    <v-flex md3>
                      <v-text-field
                        v-formata-moeda="form.valorIr"
                        v-money="money"
                        v-validate="'required'"
                        v-model="form.valorIr"
                        :disabled="disabled"
                        :error-messages="errors.collect('Valor IR')"
                        data-vv-name="Valor IR"
                        type="tel"
                        prefix="%"
                        maxlength="5"
                        reverse
                        label="Valor IR"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-formata-moeda="form.valorServicos"
                        v-money="money"
                        v-validate="'required'"
                        v-model.lazy="form.valorServicos"
                        :disabled="disabled"
                        :error-messages="errors.collect('Valor do Serviço')"
                        data-vv-name="Valor do Serviço"
                        required
                        type="tel"
                        suffix="R$"
                        reverse
                        label="Valor do Serviço"
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-formata-moeda="baseCalculo"
                        v-money="money"
                        :value="baseCalculo"
                        disabled
                        suffix="R$"
                        label="Base de Cálculo"
                        reverse
                      />
                    </v-flex>
                    <v-flex md2>
                      <v-text-field
                        v-formata-moeda="valorLiquido"
                        v-money="money"
                        :value="valorLiquido"
                        disabled
                        suffix="R$"
                        type="tel"
                        reverse
                        label="Valor Líquido"
                      />
                    </v-flex>
                    <v-flex md12>
                      <v-textarea
                        v-model="form.discriminacao"
                        :disabled="disabled"
                        type="text"
                        label="Discriminação do Serviço"
                        rows="5"
                        counter="2000"
                      />
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card>
              <v-card
                v-if="!disabled"
                class="elevation-0">
                <v-card-title>
                  <h3 class="headline mb-0">Certificado digital</h3>
                </v-card-title>
                <v-card-text>
                  <v-layout
                    row
                    wrap>
                    <v-flex md12>
                      <label
                        for="certificado"
                        style="cursor: pointer"
                        class="caption"
                        title="Informe o certificado">
                        {{ formData ? formData.get('name') : 'Informe o certificado' }}
                        <input
                          id="certificado"
                          type="file"
                          @change="anexarCertificacao($event)"
                        >
                      </label>
                    </v-flex>
                    <v-flex md12>
                      <v-text-field
                        v-model="form.senha"
                        label="Senha Certificado"
                        type="password"
                      />
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-card-text>
          </v-card>
          <v-layout class="justify-end">
            <template
              v-if="disabled"
            >
              <v-btn
                outline
                style="color: black !important"
                @click="cancelar">Voltar</v-btn>
            </template>
            <template
              v-else
            >
              <v-btn
                outline
                style="color: black !important"
                @click="cancelar">Cancelar</v-btn>
              <v-btn
                :disabled="!valid"
                type="submit"
                color="primary">Enviar RPS</v-btn>
            </template>
          </v-layout>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { RpsBusiness, TiposBusiness } from '../../business'
import { MONEY, TODAS_ID } from '../../constants'
import dateUtils from '../../utils/dateUtils'
import numberUtils from '../../utils/numberUtils'

export default {
  data () {
    return {
      money: MONEY,
      formData: null,
      disabled: false,
      form: {
        redeId: 1,
        empresaId: 6,
        lojaId: TODAS_ID,
        senha: null,
        numeroLote: null,
        numeroRps: null,
        dataEmissao: dateUtils.currentDate(),
        naturezaOperacao: 1,
        status: null,
        correcaoErro: null,
        numeroRpsSubst: null,
        notaFiscal: null,
        numeroVerificacao: null,
        linkNota: null,
        dhNotaFiscal: null,
        valorIr: 1.50,
        valorServicos: 0,
        valorDeducoes: 0,
        itemListaServico: null,
        baseCalculo: 0,
        aliquota: 5.00,
        discriminacao: null,
        valorLiquido: 0
      },
      listNatureza: [],
      loading: false,
      valid: false,
      loadingBtn: false
    }
  },
  computed: {
    baseCalculo () {
      if (this.disabled) {
        return this.form.baseCalculo
      }
      return this.form.valorServicos
    },
    valorLiquido () {
      if (this.disabled) {
        return this.form.valorLiquido
      }
      if (this.form.valorServicos && this.form.valorIr) {
        let valorServico = numberUtils.convertStringToNumber(this.form.valorServicos)
        let desconto = numberUtils.calculaDescontoPercentual(valorServico, this.form.valorIr)
        console.log('desconto: ' + desconto)
        return numberUtils.formatValorMonetario(valorServico - desconto)
      }
      return this.form.valorServicos
    },
    mostrarBotoesDownload () {
      return this.disabled && this.form.status && this.form.status === 4
    }
  },
  beforeMount () {
    this.loading = true
    TiposBusiness.getAllNaturezaOperacaoRps()
      .then(response => { this.listNatureza = response.data })
    let loteId = this.$route.params.loteId
    // eslint-disable-next-line no-extra-boolean-cast
    if (!!loteId) {
      this.disabled = true
      RpsBusiness.getLoteRps(loteId)
        .then(response => {
          this.form = response.data
        }).finally(() => {
          this.loading = false
        })
    } else {
      this.disabled = false
      RpsBusiness.proximoNumeroRps()
        .then(response => {
          this.form.numeroRps = response.data
        }).finally(() => {
          this.loading = false
        })
    }
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.salvarRps()
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    salvarRps () {
      this.loading = this.loadingBtn = true
      this.form.valorLiquido = this.valorLiquido
      this.form.baseCalculo = this.baseCalculo
      this.formData.delete('form')
      this.formData.append('form', JSON.stringify(this.form))
      /*
      Object.keys(this.form).forEach(key => {
        this.formData.append(key, this.form[key])
      })
      */
      RpsBusiness.gerarRPS(this.formData)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.loading = this.loadingBtn = false
          this.cancelar()
        }).catch(erro => {
          this.$root.showErro(erro.data)
          this.loading = this.loadingBtn = false
        })
    },
    cancelar () {
      this.$router.push('/rps')
    },
    anexarCertificacao (event) {
      if (event && event.target && event.target.files.length > 0) {
        let file = event.target.files[0]
        this.formData = new FormData()
        this.formData.append('file', file)
        this.formData.append('name', file.name)
        /*
        var reader = new FileReader()
        reader.onload = () => {
          this.fileCertificado = reader.result
        }
        reader.readAsDataURL(this.file)
        */
      }
    },
    onChangeLoja (lojaSelecionada) {
      // TAXA DE ADM BGO COMPANY XV DE NOVEMBRO  10/07/2020   A   30/07/2020
      this.form.discriminacao = 'TAXA DE ADM '
      if (lojaSelecionada) {
        this.form.discriminacao += lojaSelecionada.descricao
      }
      this.form.discriminacao += '.\n\nIRRF DE 1,5% SOB RESPONSABILIDADE DA PRATICO ADMINISTRADORA DE CARTOES E MEIOS DE PAGAMENTO LTDA CFE IN SRF 153/87, E LEI 7450/85 ART 53 C/C LEI 9064/1995 ART 6º.'
    },
    downloadXml () {
      let blob = new Blob([this.form.xmlNota], { type: 'text/xml' })
      let url = window.URL.createObjectURL(blob)
      window.open(url)
    }
  }
}
</script>
